*{
    box-sizing: border-box;
}

.HomePage{
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: auto;
}

.filterSidebar{
    background: #f0f0fc;
    border-radius: 10px;
    left: 0;
    padding: 1rem;
    position: fixed;
    text-align: center;
    top: 30%;
    width: 14rem;
}
.priceInput{
    background-color: #7064e5;
    border-radius: 4px;
    cursor: pointer;
    height: 6px;
    -webkit-transform: scale(1);
    transform: scale(1);
    width: 90%;
}

.category{
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    justify-content: center;
    margin-top: 1rem;
}
.category div{
    display: block;
}


.homePageForm{
    margin: auto;
    width: 25%;
}

.homePageForm .search{
    background: #f4f6f8!important;
    border: 1px solid #7064e5;
    border-radius: 10px;
    box-sizing: border-box;
    color: #7064e5!important;
    font-size: 1.2em;
    font-weight: 400;
    line-height: 1.5;
    margin: 1em 0!important;
    outline: none;
    padding: 12px;
    width: 100%;
}

.productList{
    grid-gap: 2rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3,1fr);
    margin: 3rem 15rem;
}

.productList h2{
    text-align: center;
    
}
